* {
    -webkit-tap-highlight-color: rgba(0,0,0,0);
}
html {
    font-size: 10px;
}
body {
    width: 100%;
    font-size: 1.6rem;
    font-family: 'Lato', 'Josefin Sans', Verdana, Arial, sans-serif;
}
h1 {
    &:not(.header-text) {
        font-weight: 300;
    }
}
section {
    box-sizing: border-box;
    padding: 30px 10px;
    text-align: center;
}
.container {
    max-width: 1150px;
    margin: 0 auto;
}
.btn {
    background: $secondary-color;
    color: $main-color;
    border-radius: 3px;
}
.btn-style {
    height: 33px;
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
}
