// }
// .twitter {
//     background: #4099FF;
// }
.main-bg {
    height: 510px;
    background: url("../images/top.jpg");
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    color: $main-color;
    flex-direction: column;
    text-align: center;
    font-weight: 300;
    position: relative;
    //Sńmall phones
    @media only screen and (max-device-height: 320px) {
        height: 300px;
    }
    //360x640 phones
    @media only screen and (max-device-height: 640px) {
        height: 610px;
    }
    @media screen and (max-device-height: 360px) and (orientation: landscape) {
        height: 320px;
    }
    //Phones bigger than 360x640
    @media only screen and (min-device-height: 700px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: portrait) {
        height: 660px;
    }
    @media only screen and (min-device-height: 400px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: landscape) {
        height: 375px;
    }
    h1,
    p {
        width: 90%;
        margin: 0 auto;
        padding: 15px 0;
        line-height: 1.5;
    }
    .icon-container {
        display: none;
    }
}
.mobile-menu {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%,0);
    width: 100%;
    background: rgba(159, 68, 155, 0.4);
    div {
        display: flex;
        justify-content: space-between;
        box-sizing: border-box;
        padding: 5px 10px;
    }
    ul {
        font-size: 2rem;
        text-transform: uppercase;
        display: none;
        list-style: none;
        padding: 0;
        height: 320px;
        @media (orientation:landscape) {
            margin: 0;
            height: 200px;
        }
        li {
            padding: 10px 0;
            @media (orientation:landscape) {
                display: inline-block;
                padding: 10px 0;
                width: 49%;
            }
            a {
                text-decoration: none;
                color: $main-color;
            }
        }
    }
}
.scrollTop-arrow {
    text-decoration: none;
    font-size: 100px;
    color: $secondary-color;
    position: fixed;
    bottom: 15px;
    right: 5px;
    width: 50px;
    height: 50px;
    z-index: 999;
}
.big-logo {
    display: none;
}
.sticky-menu {
    display: none;
}
.visible {
    display: block !important;
}
.hidden {
    font-size: 0 !important;
}
.hide {
    display: none !important;
}
.bg-grey {
    background: $background-color;
}
.section-header-div {
    display: flex;
    justify-content: center;
    padding: 0 5px 30px;
    width: 100%;
    font-family: "Existence-Light";
    span {
        color: $secondary-color;
    }
    &:before {
        align-self: flex-end;
        content: " ";
        width: 20px;
        height: 20px;
        border-bottom: 1px solid $secondary-color;
        border-left: 1px solid $secondary-color;
    }
    &:after {
        align-self: flex-start;
        content: " ";
        width: 20px;
        height: 20px;
        border-top: 1px solid $secondary-color;
        border-right: 1px solid $secondary-color;
    }
}
.button-holder {
    display: flex;
    justify-content: space-around;
}
.aboutUs-section {
    a {
        cursor: pointer;
        margin: 5px auto;
    }
}
#nextMember,
#prevMember {
    cursor: pointer;
    width: 50px;
    margin: 0 0 30px;
    position: relative;
    &:active {
        top: 1px;
    }
}
.team-members-slider {
    margin: 0 auto;
    width: 290px;
    overflow: hidden;
    font-size: 0;
    ul {
        margin: 0;
        padding: 0;
        position: relative;
        li {
            font-size: 2rem;
            display: inline-block;
            width: 290px;
            div {
                display: flex;
                flex-direction: column;
                img {
                    border-radius: 50%;
                    &:hover {
                        -webkit-box-shadow: 5px 5px 10px 0 rgba(209,209,209,1);
                        -moz-box-shadow: 5px 5px 10px 0 rgba(209,209,209,1);
                        box-shadow: 5px 5px 10px 0 rgba(209,209,209,1);
                    }
                }
                p {
                    margin: 5px 0;
                    line-height: 1.4;
                }
            }
        }
    }
    span {
        color: #cdcdcd;
    }
}
.member-pic {
    border-radius: 50%;
    margin: 0 auto;
}
.skill-desc {
    padding: 15px 0;
    display: flex;
    justify-content: space-between;
    color: black;
    font-weight: 700;
    text-transform: uppercase;
    width: 80%;
    margin: 0 auto;
    span {
        font-size: 0;
        font-weight: 300;
    }
}
.skill-show {
    background: #f0f0f0;
    width: 80%;
    margin: 0 auto;
    height: 20px;
    span {
        width: 0;
        display: block;
        height: 20px;
        background: $secondary-color;
    }
}
.animate {
    transition: width 3s ease-out;
    &.web-design {
        width: 90%;
    }
    &.graphic-design {
        width: 70%;
    }
    &.html-css {
        width: 75%;
    }
    &.ui-ux {
        width: 85%;
    }
}
.slider-citations {
    margin: 0 auto;
    width: 290px;
    overflow: hidden;
    font-size: 0;
    padding: 15px 0;
    ul {
        margin: 0;
        padding: 0;
        position: relative;
        li {
            font-size: 1.5rem;
            display: inline-block;
            width: 290px;
            height: 350px;
            div {
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                img {
                    border-radius: 50%;
                }
                p {
                    margin: 5px 0;
                    line-height: 1.4;
                }
            }
        }
    }
}
.slider-citations-btns {
    margin: 5px 0;
    padding: 0 5px;
    span {
        cursor: pointer;
        display: inline-block;
        margin: 0 5px;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        border: 2px solid #cdcdcd;
    }
}
.active {
    background: #cdcdcd;
}
.statistics {
    display: inline-block;
    font-size: 3rem;
    width: 49%;
    &:after {
        content: "Lorem ipsum";
        font-size: 1.4rem;
        display: block;
    }
}
.gallery-buttons-holder {
    @extend .button-holder;
    flex-wrap: wrap;
}
.gallery-buttons {
    border-radius: 3px;
    background: none;
    color: black;
    border: 1px solid black;
    text-decoration: none;
    text-transform: uppercase;
    flex: 0.5 45%;
    margin: 2px;
    &:hover {
        background: $secondary-color;
        border: 1px solid $secondary-color;
        color: $main-color;
    }
}
.img-container {
    img {
        z-index: 10;
    }
    img:not(.fullScreen) {
        display: block;
        width: 100%;
    }
}
.enlarge {
    transition: all 0.7s ease-in;
    &:hover {
        transform: scale(1.2);
    }
}
.img-background {
    overflow: hidden;
}
.fullScreen {
    position: fixed;
    width: 95% !important;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    @media screen and (orientation: landscape) {
        width: 45% !important;
    }
}
.images-hidden {
    display: none;
}
.contact-method {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    padding: 5px 10px;
    * {
        margin: 10px 0;
    }
    input,
    textarea {
        border: 1px solid grey;
        padding: 10px;
        resize: vertical;
    }
    button {
        border: 0;
    }
}
.address-info {
    p {
        font-weight: normal;
        &:not(:first-child) {
            font-weight: bold;
        }
    }
}
.section-bg {
    height: 300px;
    background: url("../images/layer4.jpg");
    background-size: cover;
    background-position: center;
    color: $main-color;
    display: flex;
    align-items: center;
}
.galleryOverlay {
    width: 100%;
    height: 100%;
    position: fixed;
    overflow: hidden;
    top: 0;
    left: 0;
    background: black;
    opacity: 0.85;
    display: none;
    z-index: 9;
}
@font-face {
    font-family: 'Existence-Light';
    src: url('../fonts/Existence-Light.eot'), url('../fonts/Existence-Light.woff') format('woff'), url('../fonts/Existence-Light.ttf') format('truetype');
}
