@include breakpoint(large-mobile) {
    .shortDesc-section {
        picture {
            width: 20%;
            img {
                min-width: 76px;
                width: 100%;
                max-width: 108px;
            }
        }
        div {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
        }
        h2 {
            width: 75%;
        }
        p {
            width: 100%;
        }
    }
    .statistics {
        width: 30%;
    }
    .img-container figure {
        display: inline-block;
        margin: 0;
        width: 49%;
        padding: 10px 0;
    }
}
