@include breakpoint(large-desktop) {
    html {
        font-size: 11px;
    }
    .btn-style {
        height: 40px;
        width: 120px;
    }
    .main-bg {
        .nav-buttons {
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            width: 100%;
            height: 200px;
            .upper-navBtn {
                display: flex;
                align-self: flex-start;
            }
        }
    }
    .sticky-menu {
        display: none;
        max-width: 1150px;
        margin: 0 auto;
        img {
            margin: 0;
        }
        ul {
            margin: 0;
        }
    }
    .shortDesc-section {
        div {
            width: 24%;
            display: inline-block;
        }
    }
    .about-content-text p {
        width: 65%;
    }
    .team-members-slider {
        width: 870px;
    }
    .slider-citations {
        width: 450px;
        ul {
            li {
                font-size: 2rem;
                width: 450px;
            }
        }
    }
    .statistics {
        width: 24%;
    }
    .gallery-buttons-holder {
        width: 33%;
        margin: 0 auto;
    }
    .gallery-buttons {
        width: 70px;
        flex: none;
    }
    .img-container figure {
        width: 33%;
    }
    .section-bg {
        height: 400px;
    }
}
