@include breakpoint(tablet) {
    html {
        font-size: 12px;
    }
    // .btn-style {
    //     height: 45px;
    //     width: 120px;
    // }
    .main-bg {
        height: 630px;
        .icon-container {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;
            align-items: center;
            flex: 0 0 30%;
            img {
                max-width: 10vw;
                padding: 0 5px;
                &:hover {
                    background: url("../images/shape3.svg") no-repeat center;
                    background-size: contain;
                }
            }
            .scroller {
                width: 100%;
                img {
                    max-width: 5vw;
                }
            }
        }
        .big-logo {
            display: block;
        }
    }
    .sticky-menu {
        display: none;
    }
    .sticky {
        display: flex !important;
        position: fixed;
        width: 100%;
        top: 0;
        left: 50%;
        background: $main-color;
        padding: 0 10px;
        box-sizing: border-box;
        border-radius: 10px;
        z-index: 2;
        border: 1px solid grey;
        transform: translate(-50%,0);
        height: 80px;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        img {
            margin: 0 auto;
        }
        ul {
            list-style: none;
            display: flex;
            text-transform: uppercase;
            margin: 0 auto;
            padding: 0;
            li {
                padding-left: 15px;
                a {
                    text-decoration: none;
                    color: black;
                    &:hover {
                        color: $secondary-color;
                    }
                }
            }
        }
    }
    .mobile-menu,
    .scrollTop-arrow {
        display: none;
    }
    .shortDesc-section {
        div {
            width: 49%;
            display: inline-block;
            h2 {
                width: 100%;
            }
            p {
                width: 100%;
                box-sizing: border-box;
                padding: 0 10px;
            }
        }
    }
    .aboutUs-section {
        a {
            margin: 5px 0;
        }
        picture {
            flex: 0 0 48%;
        }
    }
    .about-content {
        display: flex;
        img,
        source {
            width: 100%;
            max-width: 450px;
        }
        picture {
            flex: 0 0 50%;
            padding-right: 10px;
            text-align: left;
        }
    }
    .about-content-text {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 0 10px;
        flex: 0 0 47%;
        p {
            margin: 0;
            text-align: left;
        }
    }
    .about-content-full {
        min-height: 300px;
        display: block;
        img,
        source {
            width: 100%;
            max-width: 450px;
        }
        picture {
            float: left;
            width: 50%;
            text-align: left;
            padding-right: 10px;
        }
        p:first-child {
            margin-top: 0;
        }
    }
    .about-content-text-full {
        padding: 0 10px;
        text-align: left;
        a {
            margin: 5px auto;
        }
    }
    .team-members-slider {
        width: 580px;
        figure {
            margin: 5px 0;
        }
    }
    .skill-section {
        div {
            &:not(.section-header-div, .skill-show) {
                display: inline-block;
                width: 49%;
            }
        }
    }
    .statistics {
        display: inline-block;
        width: 25%;
        font-size: 3rem;
        &:after {
            content: "Lorem ipsum";
            font-size: 1.4rem;
            display: block;
        }
    }
    #watch-more {
        height: 40px;
        width: 120px;
    }
    .gallery-buttons {
        margin: 5px;
        padding: 5px;
        flex: 0.5 20%;
    }
    .img-container {
        figcaption {
            padding: 10px 0;
        }
    }
    .contact-section {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        .contact-method {
            width: 47%;
            height: 450px;
        }
        .address-info {
            width: 47%;
            padding: 5px 10px;
            height: 450px;
            p {
                &:first-child {
                    margin-top: 5px;
                }
            }
        }
        .bottom-logo {
            width: 100%;
        }
    }
}
